/*GameBoard.css*/
.game-board {
	display: grid;
	grid-template-columns: 80px 40px auto 40px 80px 80px;
	grid-template-rows: 40px 80px 40px 40px 80px 80px;
	width: 640px;
	height: 360px;
	margin: 40px auto;
}
.board-area.plot-deck {
  grid-column: 1 / 3;
  grid-row: 6 / 6;
}
.board-area.used-plot-cards {
  grid-column: 1 / 3;
  grid-row: 5 / 5;
}
.board-area.characters {
  grid-column: 3 / 7;
  grid-row: 2 / 4;
}
.board-area.locations {
  grid-column: 3 / 4;
  grid-row: 4 / 7;
}
.board-area.hand {
  grid-column: 6 / 6;
  grid-row: 6 / 6;
}
.board-area.faction-card {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
}
.board-area.draw-deck {
  grid-column: 5 / 6;
  grid-row: 4 / 6;
}
.board-area.discard-pile {
  grid-column: 6 / 7;
  grid-row: 4 / 6;
}
.board-area.dead-pile {
  grid-column: 4 / 6;
  grid-row: 6 / 6;
}